import React, { useState, useEffect } from 'react';

import ContactForm from './ContactForm';
import Filter from './Filter';
import ContactList from './ContactList';
import s from './App.module.css';

export default function App () {
  const [contact, setContact] = useState([]);
  const [filter, setFilter] = useState("");

  useEffect(() => {
    if (localStorage.getItem('contacts')?.length > 0) {
      setContact(JSON.parse(localStorage.getItem('contacts')));
    }
  }, [])

  useEffect(() => {
    localStorage.setItem('contacts', JSON.parse(contact))
  }, [contact])

  const addContact = newContact => {
    const contactCheck = contact.find(
      ({ name }) => name === newContact.name,
    );

    if (contactCheck !== undefined) {
      alert(`${newContact.name} is already in contact`);
      return;
    }

    setContact(p => [...p, newContact]);
  };

  const removeContact = id => {
    setContact(
      contact.filter(contact => contact.id !== id),
    );
  };

  const onChange = (e) => {
    setFilter(e.currentTatget.value)
  };

    const lowerCasedFilter = filter.toLowerCase();
    const filteredContacts = contact.filter(({ name }) =>
      name.toLowerCase().includes(lowerCasedFilter),
    );

    return (
      <div>
        <h1 className={s.title}>Phonebook</h1>
        <ContactForm addContact={addContact} />
        <h2 className={s.title}>Contacts</h2>
        <Filter value={filter} onChange={onChange} />
        <ContactList
          removeContact={removeContact}
          contacts={filteredContacts}
        />
      </div>
    );
  }